<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!-- 添加按钮 -->
      <el-button type="primary" @click="addaddGoodsClassify">添加分类</el-button>
      <!-- 表格 第三方插件的树形表格 -->
      <tree-table :data="goodsClassifyList" :columns="columns" show-index border :selection-type="false" :expand-type="false" index-text="#">
        <!-- 是否可用 -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" v-if="!scope.row.cat_deleted" style="color:lightgreen"></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1">二级</el-tag>
          <el-tag type="warning" v-else>三级</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="operate" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" @click="editGoodsClassify(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="deleteGoodsClassify(scope.row.cat_id)">删除</el-button>
        </template>
      </tree-table>
      <!-- 分页 -->
      <el-pagination @size-change="pageSizeHandle" @current-change="currentPageHandle" :current-page="pageInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="pageInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!-- dialog--添加商品分类  -->
    <el-dialog title="添加商品分类" :visible.sync="addGoodsClassifyVisible" @close="addGoodsClassifyClose">
      <el-form :model="addGoodsClassifyForm" :rules="addGoodsClassifyRules" ref="addGoodsClassifyRef" label-width="100px">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addGoodsClassifyForm.cat_name"></el-input>
        </el-form-item>
        <!-- 级联选择器 -->
        <el-form-item label="父级分类">
          <el-cascader ref="cascaderRef" v-model="selectKeys" :options="parentGoodsClassify" :props="cascadeProps" @change="cascadeChange" clearable></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addGoodsClassifyVisible = false">取 消</el-button>
        <el-button type="primary" @click="addaddGoodsClassifySure">确 定</el-button>
      </span>
    </el-dialog>
    <!-- dialog--编辑商品分类 -->
    <el-dialog title="编辑商品分类" :visible.sync="editGoodsClassifyVisible">
      <el-form :model="editGoodsClassifyForm" :rules="editGoodsClassifyRules" ref="editGoodsClassifyRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="editGoodsClassifyForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editGoodsClassifyVisible = false">取 消</el-button>
        <el-button type="primary" @click="editGoodsClassifySure">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  created() {
    this.initData()
  },
  data() {
    return {
      goodsClassifyList: [], // 商品分类列表数据

      // 分页/请求数据的参数
      pageInfo: {
        type: 3,
        pagenum: 1,
        pagesize: window.sessionStorage.getItem('goodsClassifyPageSize')
          ? parseInt(window.sessionStorage.getItem('goodsClassifyPageSize'))
          : 5,
      }, // 初始化请求数据的参数
      total: 0, // 商品分类(1级)数据总数

      // 为tree-table指定列的定义
      columns: [
        {
          label: '分类名称', // 列标题名称
          prop: 'cat_name', // 该列的内容
        },
        {
          label: '是否可用',
          type: 'template', // 是否使用模板
          template: 'isok', // 模板名称(自定义)
        },
        {
          label: '排序',
          type: 'template',
          template: 'order',
        },
        {
          label: '操作',
          type: 'template',
          template: 'operate',
        },
      ],

      // 添加商品分类的dialog显示与隐藏
      addGoodsClassifyVisible: false,
      // 添加商品的级联选择中需要使用的父级商品分配权限
      parentGoodsClassify: [],
      // 添加商品中级联组件的定义
      cascadeProps: {
        label: 'cat_name', // 看到的名称
        value: 'cat_id', // 选中的分类id
        children: 'children', // 子级嵌套
        expandTrigger: 'hover', // 悬停显示下一个级别列表
        checkStrictly: true, // 默认可以选中父级分类
      },
      // 选中的父级分类的id信息,必须是数组，存放着选中的多级的分类id
      selectKeys: [],
      // 添加商品分类的表单信息
      addGoodsClassifyForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0,
      },
      // 添加商品分类表单的验证规则
      addGoodsClassifyRules: {
        cat_name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
        ],
      },

      // 编辑商品分类dialog的显示与隐藏
      editGoodsClassifyVisible: false,
      // 编辑商品分类所需的api信息
      editGoodsClassifyForm: {},
      // 编辑商品分类表单的验证规则
      editGoodsClassifyRules: {
        cat_name: [
          { required: true, message: '请输入商品分类名称', trigger: 'blur' },
        ],
      },
    }
  },
  methods: {
    // 初始化获取商品分类数据
    async initData() {
      let { data: res } = await this.$http.get('categories', {
        params: this.pageInfo,
      })
      if (res.meta.status !== 200)
        return this.$message.error('获取商品分类数据失败！')
      this.goodsClassifyList = res.data.result
      this.total = res.data.total
    },

    // 当前分页显示的条数
    pageSizeHandle(newSize) {
      this.pageInfo.pagesize = newSize
      window.sessionStorage.setItem('goodsClassifyPageSize', newSize)
      this.initData()
    },
    // 当前页数
    currentPageHandle(newPage) {
      this.pageInfo.pagenum = newPage
      this.initData()
    },

    // 添加商品分类的dialog打开
    addaddGoodsClassify() {
      // 获取所有的一级二级商品分类信息
      this.getFatherClassify()

      this.addGoodsClassifyVisible = true
    },
    // 添加商品分类dialog关闭
    addGoodsClassifyClose() {
      this.$refs.addGoodsClassifyRef.resetFields()
      this.selectKeys = []
      this.addGoodsClassifyForm.cat_pid = 0
      this.addGoodsClassifyForm.cat_level = 0
    },
    // 获取所有父级分类的商品分类信息
    async getFatherClassify() {
      let { data: res } = await this.$http.get('categories', {
        params: {
          type: 2,
        },
      })
      if (res.meta.status !== 200)
        return this.$message.error('获取商品父级分类信息失败！')
      this.parentGoodsClassify = res.data
    },
    // 级联选择器,选择项发生变化触发
    cascadeChange() {
      // 选中项变化，会自动同步到selectKeys中
      console.log(this.selectKeys)
      // 选中单选框会自动隐藏级联菜单
      this.$refs.cascaderRef.toggleDropDownVisible(false)
      // 判断选中的级联组件的数组长度，如果为0表示没有选中任何一项，为1则选中一级标签，为2选中二级标签
      if (this.selectKeys.length > 0) {
        // 父级id为选中数组的最后一项
        this.addGoodsClassifyForm.cat_pid =
          this.selectKeys[this.selectKeys.length - 1]
        // 层级为数组的长度
        this.addGoodsClassifyForm.cat_level = this.selectKeys.length
      } else {
        this.addGoodsClassifyForm.cat_pid = 0
        this.addGoodsClassifyForm.cat_level = 0
      }
    },
    // 添加分类
    addaddGoodsClassifySure() {
      this.$refs.addGoodsClassifyRef.validate(async (valid) => {
        if (!valid) return false
        let { data: res } = await this.$http.post('categories', {
          cat_pid: this.addGoodsClassifyForm.cat_pid,
          cat_name: this.addGoodsClassifyForm.cat_name,
          cat_level: this.addGoodsClassifyForm.cat_level,
        })
        if (res.meta.status !== 201) return this.$message.error('添加分类失败')
        let str = this.cascaderLabel() // 获取级联选择器中的label值
        if (str === undefined) {
          this.$message.success(
            `添加分类【${this.addGoodsClassifyForm.cat_name}】成功`
          )
        } else {
          this.$message.success(`添加分类【${str}】成功`)
        }

        this.addGoodsClassifyVisible = false
        this.initData()
      })
    },
    // 获取级联组件的label
    cascaderLabel() {
      const checkedNodes = this.$refs['cascaderRef'].getCheckedNodes()
      if (checkedNodes.length === 0) {
        return
      }
      let textArr = checkedNodes[0].pathLabels
      let arr = []
      for (let i = 0; i < textArr.length; i++) {
        arr.push(textArr[i])
      }
      let str = arr.join('/') + '>' + this.addGoodsClassifyForm.cat_name
      return str
    },

    // 编辑分类dialog打开
    editGoodsClassify(info) {
      this.editGoodsClassifyVisible = true
      this.editGoodsClassifyForm = info
    },
    // 编辑分类名称
    async editGoodsClassifySure() {
      let { data: res } = await this.$http.put(
        'categories/' + this.editGoodsClassifyForm.cat_id,
        {
          cat_name: this.editGoodsClassifyForm.cat_name,
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('修改商品分类名称失败！')
      this.$message.success('修改商品分类名称成功！')
      this.initData()
      this.editGoodsClassifyVisible = false
    },

    // 删除商品分类
    async deleteGoodsClassify(id) {
      let confirm = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      if (confirm === 'cancel') return
      let { data: res } = await this.$http.delete('categories/' + id)
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('删除商品分类失败')
      this.$message.success('删除商品分类成功！')
      // 当前页数据被删完了,自动更新到有数据的页。如果已经是第一页就不动
      if (this.goodsClassifyList.length === 1) {
        this.pageInfo.pagenum =
          this.pageInfo.pagenum === 1 ? 1 : this.pageInfo.pagenum - 1
      }
      this.initData()
    },
  },
}
</script>

<style lang="less" scoped>
.zk-table {
  margin-top: 20px;
}
</style>